html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.block {
  display: flex;
}

* {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: 'bebas_neue';
  src: url("/fonts/bebas_neue.woff");
}

@font-face {
  font-family: 'bebasneue_bold';
  src: url("/fonts/bebasneue_bold.woff");
}

@font-face {
  font-family: 'bebasneue_book';
  src: url("/fonts/bebasneue_book.woff");
}

@font-face {
  font-family: 'bebasneue_light';
  src: url("/fonts/bebasneue_light.woff");
}

@font-face {
  font-family: 'bebasneue_regular';
  src: url("/fonts/bebasneue_regular.woff");
}

@font-face {
  font-family: 'bebasneue_thin';
  src: url("/fonts/bebasneue_thin.woff");
}

.container {
  width: 940px;
  margin: auto;
  margin-background: #FFF url(../img/img/bg.png) no-repeat center;
}

.header__menu {
  float: right;
  padding-top: 15px;
}

.header__menu ul li {
  cursor: pointer;
  display: inline;
}

.header__menu ul li:nth-child(5) {
  margin-right: 0px;
}

.header__a {
  text-decoration: none;
  color: #b9b9b9;
  font-size: 17px;
  font-weight: 400;
  text-transform: uppercase;
  margin-left: 28px;
  font-family: bebas_neue, sans-serif;
}

.header__a:hover {
  color: #f3af9d;
}

._active {
  color: #e8603c;
}

.h2 {
  width: 47px;
  height: 23px;
  color: #ffffff;
  font-family: bebas_neue, sans-serif;
  font-size: 24px;
  font-weight: 400;
  padding-top: 19px;
  padding-left: 19px;
}

.header__logo {
  float: left;
  width: 87px;
  padding-top: 20px;
  padding-left: 5px;
  height: 34px;
  color: #e8603c;
  font-size: 20px;
  text-transform: uppercase;
  font-family: bebasneue_book, sans-serif;
}

.header__logo span {
  color: #f3af9d;
}

.background_rectangles {
  padding-top: 78px;
  padding-bottom: 30px;
}

.center__rectangle {
  width: 940px;
  height: 61px;
  background-color: #e8603c;
  margin: auto;
}

.left__rectangle {
  float: left;
}

.right__rectangle {
  float: right;
}

.side__rectangle {
  width: 180px;
  height: 61px;
  background-color: rgba(232, 96, 60, 0.5);
}

.post__statistic {
  float: left;
}

.small__red__text {
  font-size: 12px;
  text-transform: uppercase;
  color: #e8603c;
  font-family: bebas_neue, sans-serif;
}

.small__grey__text {
  font-family: HelveticaNeue, sans-serif;
  font-size: 12px;
  color: #bababa;
}

.stats__margin {
  margin-bottom: 5px;
}

.blog__block {
  position: relative;
  width: 100%;
  height: auto;
  padding-bottom: 30px;
}

.blog__icon {
  padding-top: 20px;
  position: absolute;
  left: -90px;
  height: 75px;
  width: 75px;
  background-color: #e8603c;
  text-align: center;
}

.post__text {
  padding-left: 150px;
  width: 670px;
}

.article {
  color: #e8603c;
  font-family: bebasneue_book, sans-serif;
  font-size: 18px;
}

.post__text__info {
  font-size: 12px;
  color: #414141;
  font-family: HelveticaNeue, sans-serif;
}

.dots__line {
  padding-top: 20px;
  background-image: url("/img/img/tile.png");
  height: 8px;
}

.blog__small__img {
  position: absolute;
  top: 20px;
  left: 34px;
  height: 75px;
  float: left;
}

.blog__first__topic {
  float: left;
  padding-bottom: 30px;
}

.quote__text__info {
  padding-top: 25px;
  padding-left: 70px;
  font-size: 13px;
  width: 670px;
  font-family: HelveticaNeue, sans-serif;
  font-style: italic;
  color: #474747;
}

.quote__block {
  background-color: #f5f5f5;
  width: 670px;
}

.link__text {
  padding: 0;
}

.block__sidebar {
  display: block;
  float: right;
  width: 25%;
}

.sidebar__shortext {
  font-family: HelveticaNeue, sans-serif;
  color: #464646;
  font-size: 12px;
}

.sidebar__a {
  text-decoration: none;
  font-family: HelveticaNeue, sans-serif;
  color: #464646;
  font-size: 12px;
}

.sidebar__a:hover {
  color: #e8603c;
}

.gallery__photo {
  width: 50px;
  height: 50px;
  border: 1px solid #e8603c;
  background-color: #f3af9d;
  display: inline-block;
  margin-right: 5px;
}

.medium__red__text {
  font-family: bebas_neue, sans-serif;
  font-size: 14px;
  color: #e8603c;
  text-transform: uppercase;
}

.pagination {
  margin-bottom: 50px;
}

.pagination__li {
  display: inline-block;
  width: 26px;
  height: 26px;
  background: #f3b29d;
  font-family: bebas_neue, sans-serif;
  cursor: pointer;
}

.pagination__li:hover {
  background: #e8663c;
}

.pagination__a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  line-height: 30px;
  padding-left: 9px;
}

.pagination_active {
  background: #e8603c;
}

.hr {
  width: 100%;
  height: 8px;
  background-image: url("/img/img/tile.png");
}

.hr_article {
  margin: 10px 0px 50px;
}

.fix__hr {
  width: 71%;
}

.fix__img {
  margin-top: 490px;
}

.slick-prev, .slick-next {
  position: absolute;
  left: 10px;
  top: 50%;
  width: 50px;
  background: pink;
  height: 50px;
  transform: translate(0, -50%);
}

.gallery .img {
  width: 300px;
  height: 200px;
  line-height: 200px;
  font-size: 100px;
  text-align: center;
  border: 1px solid tomato;
}

.gallery .slick-prev, .gallery .slick-next {
  position: absolute;
  left: 10px;
  top: 50%;
  width: 50px;
  background: pink;
  height: 50px;
  transform: translate(0, -50%);
}

.gallery .slick-next {
  left: auto;
  right: 10px;
}
