// done by riverco.de
@import "helpers/all";
@import "generated/sprite-svg";
@import "common";
@import "layouts";
@import "main";

* {
  margin: 0;
  padding: 0; }

@mixin bebas_neue {
   font-family: 'bebas_neue',sans-serif; }
@font-face {
   font-family: 'bebas_neue';
   src: url('/fonts/bebas_neue.woff'); }

@mixin bebasneue_bold {
  font-family: 'bebasneue_bold',sans-serif; }
@font-face {
  font-family: 'bebasneue_bold';
  src: url('/fonts/bebasneue_bold.woff'); }

@mixin bebasneue_book {
  font-family: 'bebasneue_book',sans-serif; }
@font-face {
  font-family: 'bebasneue_book';
  src: url('/fonts/bebasneue_book.woff'); }

@mixin bebasneue_light {
  font-family: 'bebasneue_light',sans-serif; }
@font-face {
  font-family: 'bebasneue_light';
  src: url('/fonts/bebasneue_light.woff'); }

@mixin bebasneue_regular {
  font-family: 'bebasneue_regular',sans-serif; }
@font-face {
  font-family: 'bebasneue_regular';
  src: url('/fonts/bebasneue_regular.woff'); }

@mixin bebasneue_thin {
  font-family: 'bebasneue_thin',sans-serif; }
@font-face {
  font-family: 'bebasneue_thin';
  src: url('/fonts/bebasneue_thin.woff'); }


.container {
  width: 940px;
  margin: auto {
    background: #FFF url(../img/img/bg.png) no-repeat center; } }

//header
.header__menu {
  float: right;
  padding-top: 15px;
  ul li {
    cursor: pointer;
    display: inline;
    &:nth-child(5) {
      margin-right: 0px; } } }

.header__a {
  text-decoration: none;
  color: #b9b9b9;
  font-size: 17px;
  font-weight: 400;
  text-transform: uppercase;
  margin-left: 28px;
  font-family: bebas_neue, sans-serif;
  &:hover {
    color: #f3af9d; } }

._active {
  color: #e8603c; }



.h2 {
  width: 47px;
  height: 23px;
  color: #ffffff;
  font-family: bebas_neue, sans-serif;
  font-size: 24px;
  font-weight: 400;
  padding-top: 19px;
  padding-left: 19px; }

.header__logo {
  float: left;
  width: 87px;
  padding-top: 20px;
  padding-left: 5px;
  height: 34px;
  color: #e8603c;
  font-size: 20px;
  text-transform: uppercase;
  font-family: bebasneue_book, sans-serif; }

.header__logo span {
  color: #f3af9d; }

.background_rectangles {
  padding-top: 78px;
  padding-bottom: 30px; }

.center__rectangle {
  width: 940px;
  height: 61px;
  background-color: #e8603c;
  margin: auto; }

.left__rectangle {
  float: left; }

.right__rectangle {
  float: right; }

.side__rectangle {
  width: 180px;
  height: 61px;
  background-color: rgba(232, 96, 60, 0.5); }


//blog__block
.post__statistic {
  float: left; }

.small__red__text {
  font-size: 12px;
  text-transform: uppercase;
  color: #e8603c;
  font-family: bebas_neue, sans-serif; }


.small__grey__text {
  font-family: HelveticaNeue, sans-serif;
  font-size: 12px;
  color: #bababa; }

.stats__margin {
  margin-bottom: 5px; }


.blog__block {
  position: relative;
  width: 100%;
  height: auto;
  padding-bottom: 30px; }

.blog__icon {
  padding-top: 20px;
  position: absolute;
  left: -90px;
  height: 75px;
  width: 75px;
  background-color: #e8603c;
  text-align: center; }

.post__text {
  padding-left: 150px;
  width: 670px; }

.article {
  color: #e8603c;
  font-family: bebasneue_book, sans-serif;
  font-size: 18px; }

.post__text__info {
  font-size: 12px;
  color: #414141;
  font-family: HelveticaNeue, sans-serif; }



.dots__line {
  padding-top: 20px;
  background-image: url('/img/img/tile.png');
  height: 8px; }


.blog__small__img {
  position: absolute;
  top: 20px;
  left: 34px;
  height: 75px;
  float: left; }

.blog__first__topic {
  float: left;
  padding-bottom: 30px; }

.quote__text__info {
  padding-top: 25px;
  padding-left: 70px;
  font-size: 13px;
  width: 670px;
  font-family: HelveticaNeue, sans-serif;
  font-style: italic;
  color:  #474747; }

.quote__block {
  background-color: #f5f5f5;
  width: 670px; }

.link__text {
  padding: 0; }

//sidebar
.block__sidebar {
  display: block;
  float: right;
  width: 25%; }

.sidebar__shortext {
  font-family: HelveticaNeue, sans-serif;
  color:  #464646;
  font-size: 12px; }

.sidebar__a {
  text-decoration: none;
  font-family: HelveticaNeue, sans-serif;
  color:  #464646;
  font-size: 12px;
  &:hover {
    color: #e8603c; } }



.gallery {}


.gallery__photo {
  width: 50px;
  height: 50px;
  border: 1px solid #e8603c;
  background-color: #f3af9d;
  display: inline-block;
  margin-right: 5px; }

.medium__red__text {
  font-family: bebas_neue, sans-serif;
  font-size: 14px;
  color:  #e8603c;
  text-transform: uppercase; }







//pagination
.pagination {
  margin-bottom: 50px; }

.pagination__li {
  display: inline-block;
  width: 26px;
  height: 26px;
  background: #f3b29d;
  font-family: bebas_neue, sans-serif;
  cursor: pointer;
  &:hover {
    background: #e8663c; } }

.pagination__a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  line-height: 30px;
  padding-left: 9px; }

.pagination_active {
  background: #e8603c; }




.hr {
 width: 100%;
 height: 8px;
 background-image: url('/img/img/tile.png'); }

.hr_article {
  margin: 10px 0px 50px; }

.fix__hr {
  width: 71%; }

.fix__img {
  margin-top: 490px; }


.slick-prev,.slick-next {
    position: absolute;
    left: 10px;
    top: 50%;
    width: 50px;
    background: pink;
    height: 50px;
    transform: translate(0,-50%); }

.gallery {
  .img {
    width: 300px;
    height: 200px;
    line-height: 200px;
    font-size: 100px;
    text-align: center;
    border: 1px solid tomato; }

  .slick-prev,.slick-next {
    position: absolute;
    left: 10px;
    top: 50%;
    width: 50px;
    background: pink;
    height: 50px;
    transform: translate(0,-50%); }
  .slick-next {
    left: auto;
    right: 10px; } }
